.button {
    background-color: $color-secondary;
    padding: 1.6rem 4rem;
    border: none;
    border-radius: 5rem;
    font-size: 1.6rem;
    font-weight: 700;
    color: $color-lightest;
    cursor: pointer;

    &__hidden {
        display: none;
    }

    &--reset {
        width: min-content;
        margin-left: auto;
        padding: 1rem 1.5rem;
        font-size: 1.2rem;

        @media only screen and (min-width: $bp-smallest) {
            padding: 1.3rem 3rem;
            font-size: 1.4rem;
        }

        @media only screen and (min-width: $bp-small) {
            padding: 1.6rem 4rem;
            font-size: 1.6rem;
        }
    }
}